import { JSX } from 'react';

import {
  getCommonButtonTypeDefault,
  getCommonClassNameDefault,
} from '@skatteetaten/ds-core-utils';
import { Icon } from '@skatteetaten/ds-icons';
import { Spinner } from '@skatteetaten/ds-progress';

import {
  getIconButtonBrightnessDefault,
  getIconButtonSizeDefault,
} from './defaults';
import { IconButtonProps } from './IconButton.types';

import styles from './IconButton.module.scss';

/**
 * IconButton
 *
 * @see [Storybook](https://skatteetaten.github.io/designsystemet/?path=/docs/komponenter-iconbutton--docs) - Teknisk dokumentasjon
 * @see [Stil og tone](https://www.skatteetaten.no/stilogtone/designsystemet/komponenter/iconbutton/) - Brukerveiledning
 */
export const IconButton = ({
  ref,
  id,
  className = getCommonClassNameDefault(),
  lang,
  'data-testid': dataTestId,
  brightness = getIconButtonBrightnessDefault(),
  size = getIconButtonSizeDefault(),
  spinnerTitle,
  svgPath,
  accessKey,
  disabled,
  title,
  type = getCommonButtonTypeDefault(),
  ariaDescribedby,
  ariaExpanded,
  hasSpinner,
  isOutlined,
  onClick,
  onBlur,
  onFocus,
}: IconButtonProps): JSX.Element => {
  const sizeClassName = styles[`button_${size}`];
  const outlineClassName = isOutlined ? styles.button_outlined : '';
  return (
    <button
      ref={ref}
      id={id}
      className={`${styles.button} ${sizeClassName} ${outlineClassName} ${className}`.trim()}
      lang={lang}
      data-testid={dataTestId}
      data-brightness={brightness}
      disabled={disabled}
      accessKey={accessKey}
      type={type}
      aria-describedby={ariaDescribedby}
      aria-expanded={ariaExpanded}
      onBlur={onBlur}
      onClick={onClick}
      onFocus={onFocus}
    >
      {!hasSpinner && (
        <Icon
          className={styles.icon}
          size={size === 'extraSmall' ? 'small' : size}
          svgPath={svgPath}
          title={title}
        />
      )}
      {hasSpinner && (
        <Spinner
          className={styles.spinner}
          color={disabled ? 'black' : 'blue'}
          size={size === 'extraSmall' ? 'small' : size}
          hideTitle
        >
          {spinnerTitle}
        </Spinner>
      )}
    </button>
  );
};

IconButton.displayName = 'IconButton';
